.menu-btn{position:absolute;z-index:3;right:35px;top:35px;cursor:pointer;transition:all 0.5s ease-out}.menu-btn .btn-line{width:28px;height:3px;margin:0 0 5px 0;background:#fff;transition:all 0.5s ease-out}.menu-btn.close{transform:rotate(180deg)}.menu-btn.close .btn-line:nth-child(1){transform:rotate(45deg) translate(5px, 5px)}.menu-btn.close .btn-line:nth-child(2){opacity:0}.menu-btn.close .btn-line:nth-child(3){transform:rotate(-45deg) translate(6.5px, -6px)}.menu{position:fixed;top:0;width:100%;opacity:0.9;visibility:hidden}.menu.show{visibility:visible}.menu-branding,.menu-nav{display:flex;flex-flow:column wrap;align-items:center;justify-content:center;float:left;width:50%;height:100vh;overflow:hidden}.menu-nav{margin:0;padding:0;background:#373737;list-style:none;transform:translate3d(0%, -100%, 0);transition:all 0.5s ease-out}.menu-nav.show{transform:translate3d(0, 0, 0)}.menu-branding{background:#444;transition:all 0.5s ease-out;transform:translate3d(0%, 100%, 0)}.menu-branding.show{transform:translate3d(0, 0, 0)}.menu-branding .portrait{width:250px;height:250px;background:url("../img/jeffguitar.jpg");background-position:center;background-size:cover;border-radius:50%;border:solid 3px #eece1a}.menu .nav-item{transform:translate3d(600px, 0, 0);transition:all 0.5s ease-out}.menu .nav-item.show{transform:translate3d(0, 0, 0)}.menu .nav-item.current>a{color:#eece1a;cursor:auto}.menu .nav-link{display:inline-block;position:relative;font-size:30px;text-transform:uppercase;padding:1rem 0;font-weight:300;color:#fff;text-decoration:none;transition:all 0.5s ease-out}.menu .nav-link:hover{color:#eece1a}.nav-item:nth-child(1){transition-delay:.1s}.nav-item:nth-child(2){transition-delay:.2s}.nav-item:nth-child(3){transition-delay:.3s}.nav-item:nth-child(4){transition-delay:.4s}*{box-sizing:border-box}body{background:#444;color:#fff;height:100%;margin:0;font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;line-height:1.5}body#bg-img{position:relative;background:url(../img/portback.jpg) rgba(255,0,150,0.3);background-attachment:fixed;background-size:cover;min-height:100vh}body#bg-img:after{content:'';position:absolute;top:0;right:0;width:100%;height:100%;background:rgba(68,68,68,0.9);z-index:-1}h1,h2,h3{margin:0;font-weight:400}h1.lg-heading,h2.lg-heading,h3.lg-heading{font-size:6rem}h1.sm-heading,h2.sm-heading,h3.sm-heading{margin-bottom:2rem;padding:0.2rem 1rem;background:rgba(73,73,73,0.5)}a{color:#fff;text-decoration:none}header{position:fixed;z-index:2;width:100%}.text-secondary{color:#eece1a}main{min-height:calc(100vh - 60px);padding:4rem;justify-content:center}main .icons{margin-top:1rem}main .icons a{padding:0.4rem}main .icons a:hover{color:#eece1a;transition:all 0.5s ease-out}main#home{overflow:hidden}main#home h1{margin-top:20vh}.about-info{display:grid;grid-gap:30px;grid-template-areas:'bioimage bio bio' 'job1 job2 job3';grid-template-columns:repeat(3, 1fr)}.about-info .bio-image{grid-area:bioimage;margin:auto;border-radius:50%;border:#eece1a 3px solid}.about-info .bio{grid-area:bio;font-size:1.5rem}.about-info .job{background:#515151;padding:0.5rem;border-bottom:#eece1a 5px solid}.about-info .job-1{grid-area:job1}.about-info .job-2{grid-area:job2}.about-info .job-3{grid-area:job3}.projects{display:grid;grid-gap:20px;grid-template-columns:repeat(3, 1fr)}.projects img{width:100%;border:#fff 3px solid}.projects img:hover{opacity:0.5;border-color:#eece1a;transition:all 0.5s ease-out}.boxes{display:flex;flex-wrap:wrap;justify-content:space-evenly;align-items:center;margin-top:1rem}.boxes div{font-size:2rem;border:#fff 3px solid;padding:1.5rem 2.5rem;margin-bottom:3rem;transition:all 0.5s ease-out}.boxes div:hover{padding:1rem 2rem;background:#eece1a;color:#000}.boxes div:hover span{color:#000}#main-footer{text-align:center;padding:1rem;background:#2b2b2b;color:#fff;height:60px}.btn,.btn-dark,.btn-light{display:block;padding:0.5rem 1rem;border:0;margin-bottom:0.3rem}.btn:hover,.btn-dark:hover,.btn-light:hover{background:#eece1a;color:#000}.btn-dark{background:#000;color:#fff}.btn-light{background:#c4c4c4;color:#333}@media screen and (min-width: 1171px){.projects{grid-template-columns:repeat(4, 1fr)}}@media screen and (max-width: 768px){main{align-items:center;text-align:center}main .lg-heading{line-height:1;margin-bottom:0}main .sm-heading{margin-top:32px}ul.menu-nav,div.menu-branding{float:none;width:100%;min-height:0}ul.menu-nav.show,div.menu-branding.show{transform:translate3d(0, 0, 0)}.menu-nav{height:70vh;transform:translate3d(-100%, 0, 0);font-size:24px}.menu-branding{height:30vh;transform:translate3d(100%, 0, 0)}.menu-branding .portrait{background:url("../img/smallJeffGuitar.jpg");background-position:center;background-size:cover;width:150px;height:150px}.about-info{grid-template-areas:'bioimage' 'bio' 'job1' 'job2' 'job3';grid-template-columns:1fr}.projects{grid-template-columns:repeat(2, 1fr)}main#contact .boxes div{font-size:1.5rem}}@media screen and (max-width: 500px){main#home h1{font-size:5rem;margin-top:15vh}main#about h1,main#contact h1,main#work h1{font-size:3rem;margin-top:5vh}main#about h2,main#contact h2,main#work h2{font-size:1.5rem}main#about p,main#contact p,main#work p{font-size:1.2rem}.projects{grid-template-columns:1fr}main#contact .boxes div{font-size:1.2rem}}@media screen and (max-width: 400px){.menu .nav-link{font-size:20px}.menu .menu-branding{height:35vh}.menu .menu-nav{height:65vh}main#home h1.lg-heading{font-size:3rem}main#home h2{font-size:1.2rem}.icons{font-size:1rem}main#about h1,main#contact h1,main#work h1{font-size:3rem;margin-top:5vh}main#about h2,main#contact h2,main#work h2{font-size:1.5rem}main#about .about-info img{height:200px}main#about .about-info .bio{font-size:1rem}main#work{padding-left:35px;padding-right:35px}main#contact .boxes div{font-size:1.2rem}}@media screen and (max-width: 320px){main#home h1.lg-heading{margin-top:5vh}}
